<template>

    <v-list-item-group class="qiong-br-0"
        v-model="group" active-class="deep-blue--text text--accent-5"
    >
        <v-list-item
            v-for="tile in tiles"
            :key="tile.title"

            @click.stop="go(`/home/${tile.link}`)"
        >
            <v-list-item-avatar class="py-0 my-0">
                <v-icon v-html="tile.icon"></v-icon>
            </v-list-item-avatar>

            <v-list-item-content>
                <div tag="div">
                    {{ tile.title }}
                </div>
            </v-list-item-content>
        </v-list-item>



        <v-list-item
            @click="editChronu"
        >
            <v-list-item-avatar>
                <v-icon>mdi-application-cog</v-icon>
            </v-list-item-avatar>

            <v-list-item-content>
                <div tag="div">
                    編輯月份
                </div>
            </v-list-item-content>
        </v-list-item>

        <v-list-item
            @click="outLogin"
        >
            <v-list-item-avatar>
                <v-icon>mdi-logout-variant</v-icon>
            </v-list-item-avatar>

            <v-list-item-content>
                <div tag="div">
                    退出登錄
                </div>
            </v-list-item-content>
        </v-list-item>

    </v-list-item-group>
</template>

<script>
    export default {
        name: '',
        data() {
            return {
                group: null,
                tiles: [
                    { icon: 'mdi-bell-ring-outline', img: 'keep.png', title: '活躍分店獎勵', link: 'bonus/active_house' },
                    { icon: 'mdi-trophy-variant-outline', img: 'inbox.png', title: '首購獎勵', link: 'bonus/recommend' },
                    { icon: 'mdi-account-group-outline', img: 'hangouts.png', title: '團隊獎金', link: 'bonus/active_layer' },
                    { icon: 'mdi-clipboard-multiple-outline', img: 'messenger.png', title: '購買記錄', link: 'buys/records' },
                    { icon: 'mdi-certificate', title: '獎金報表', link: 'account/result' },
                ],
            }
        },
        methods: {
            go(path) {
                this.$router.push({
                    path
                })
            },

            outLogin() {
                this.$store.commit('changeAuth', '', null)
                this.$store.commit('saveBackendUser', '', '')

                this.$router.push({ path: '/login/' })
            },

            editChronu() {
                this.$store.commit('saveChronus', [ '', true ])
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


