<template>
    <div class="fs-12 lad-lister" v-if="me !=null">
        <div class="qiong-td">
            <div class="qiong-wide-47 pr-0">
                用户编号：{{ me.number_code }}
            </div>
            <div class="qiong-wide-60 pr-0">
                支付级别：<level-viewing :item="me.vip.named + '_' + me.vip.star" class="d-inline"></level-viewing>
            </div>
        </div>
        <div class="">
            用户全称：{{ $store.state.user_backend.display_name }}
        </div>
        <div class="qiong-menu-sub">
            IPv：{{ me.ipv }}
        </div>
    </div>

    <div v-else>
        <div>&nbsp;</div>
        <div>&nbsp;</div>
        <div>&nbsp;</div>
    </div>
</template>

<script>
import LevelViewing from '../../Viewing/LevelViewing.vue'
    export default {
  components: { LevelViewing },
        props: {
            me: {
                type: Object
            }
        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
</style>


